/**, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}*/

body {
  background: #1f1f1f;
/*  font-family: 'Open Sans', Helvetica, Arial, sans-serif;*/
}

.online_cont {
  position: relative;
  overflow: hidden;
  height: 100vh;
  padding: 80px 70px;
}
.online_cont__inner {
  position: relative;
  height: 100%;
}
.online_cont__inner:hover .online_el__bg:after {
  opacity: 1;
}

.online_el {
  position: absolute;
  left: 0;
  top: 0;
  /*width: 12%;*/
  height: 100%;
  background: #252525;
  transition: transform 0.6s 0.7s, width 0.7s, opacity 0.6s 0.7s, z-index 0s 1.3s;
  will-change: transform, width, opacity;
}
.online_el:not(.s--active) {
  cursor: pointer;
}
.online_el__overflow {
  overflow: hidden;
  position: relative;
  height: 100%;
}
.online_el__inner {
  overflow: hidden;
  position: relative;
  height: 100%;
  transition: transform 1s;
}
.online_cont.s--inactive .online_el__inner {
  transform: translate3d(0, 100%, 0);
}
.online_el__bg {
  position: relative;
  width: calc(100vw - 140px);
  height: 100%;
  transition: transform 0.6s 0.7s;
  will-change: transform;
}
.online_el__bg:before {
  content: "";
  position: absolute;
  left: 0;
  top: -5%;
  width: 100%;
  height: 110%;
  background-size: cover;
  background-position: center center;
  transition: transform 1s;
  transform: translate3d(0, 0, 0) scale(1);
}
.online_cont.s--inactive .online_el__bg:before {
  transform: translate3d(0, -100%, 0) scale(1.2);
}
.online_el.s--active .online_el__bg:before {
  transition: transform 0.8s;
}
.online_el__bg:after {
  content: "";
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0;
  transition: opacity 0.5s;
}
.online_cont.s--online_el-active .online_el__bg:after {
  transition: opacity 0.5s 1.4s;
  opacity: 1 !important;
}
.online_el__preview-cont {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transition: all 0.3s 1.2s;
}
.online_cont.s--inactive .online_el__preview-cont {
  opacity: 0;
  transform: translateY(10px);
}
.online_cont.s--online_el-active .online_el__preview-cont {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.5s;
}
.online_el__heading {
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;
}
.online_el__content {
  z-index: -1;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 30px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.1s;
}
.online_el.s--active .online_el__content {
  z-index: 2;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.5s 1.4s;
}
.online_el__text {
  text-transform: uppercase;
  font-size: 40px;
  color: #fff;
}
.online_el__close-btn {
  z-index: -1;
  position: fixed;
  right: 20px;
  top: 20px;
  width: 60px;
  height: 60px;
  opacity: 0;
  pointer-events: none;
  transition: all 0s 0.45s;
  cursor: pointer;
}
.online_el.s--active .online_el__close-btn {
  z-index: 5;
  opacity: 1;
  pointer-events: auto;
  transition: all 0s 1.4s;
}
.online_el__close-btn:before, .online_el__close-btn:after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 8px;
  margin-top: -4px;
  background: #fff;
  opacity: 0;
  transition: opacity 0s;
}
.online_el.s--active .online_el__close-btn:before, .online_el.s--active .online_el__close-btn:after {
  opacity: 1;
}
.online_el__close-btn:before {
  transform: rotate(45deg) translateX(100%);
}
.online_el.s--active .online_el__close-btn:before {
  transition: all 0.3s 1.4s cubic-bezier(0.72, 0.09, 0.32, 1.57);
  transform: rotate(45deg) translateX(0);
}
.online_el__close-btn:after {
  transform: rotate(-45deg) translateX(100%);
}
.online_el.s--active .online_el__close-btn:after {
  transition: all 0.3s 1.55s cubic-bezier(0.72, 0.09, 0.32, 1.57);
  transform: rotate(-45deg) translateX(0);
}
.online_el__index {
  overflow: hidden;
  position: absolute;
  left: 0;
  bottom: -80px;
  width: 100%;
  height: 100%;
  min-height: 250px;
  text-align: center;
  font-size: 20vw;
  line-height: 0.85;
  font-weight: bold;
  transition: transform 0.5s, opacity 0.3s 1.4s;
  transform: translate3d(0, 1vw, 0);
}
.online_el:hover .online_el__index {
  transform: translate3d(0, 0, 0);
}
.online_cont.s--online_el-active .online_el__index {
  transition: transform 0.5s, opacity 0.3s;
  opacity: 0;
}
.online_el__index-back, .online_el__index-front {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}
.online_el__index-back {
  color: #2f3840;
  opacity: 0;
  transition: opacity 0.25s 0.25s;
}
.online_el:hover .online_el__index-back {
  transition: opacity 0.25s;
  opacity: 1;
}
.online_el__index-overlay {
  overflow: hidden;
  position: relative;
  transform: translate3d(0, 100%, 0);
  transition: transform 0.5s 0.1s;
  color: transparent;
  font-size: 20vw;
}
.online_el__index-overlay:before {
  content: attr(data-index);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  transform: translate3d(0, -100%, 0);
  transition: transform 0.5s 0.1s;
}
.online_el:hover .online_el__index-overlay {
  transform: translate3d(0, 0, 0);
}
.online_el:hover .online_el__index-overlay:before {
  transform: translate3d(0, 0, 0);
}

.online_el:hover .online_el__bg:after {
  opacity: 0;
}
.online_el.s--active {
  z-index: 1;
  width: 100%;
  transform: translate3d(0, 0, 0);
  transition: transform 0.6s, width 0.7s 0.7s, z-index 0s;
}
.online_el.s--active .online_el__bg {
  transform: translate3d(0, 0, 0);
  transition: transform 0.6s;
}
.online_el.s--active .online_el__bg:before {
  transition-delay: 0.6s;
  transform: scale(1.1);
}

@media only screen and (max-width: 1000px) {

  .online_cont {
    margin-top: 20px;
    height: 98vh;
    padding: 50px 20px;
  }
  .online_cont.s--online_el-active .online_el__bg:after {
    transition: none;
    opacity: 0;
  }

  .online_cont.s--online_el-active .online_el__preview-cont {
    opacity: 0;
    transform: none;
    transition: none;
  }
  .online_cont.s--online_el-active .online_el__index {
    transition: none;
    opacity: 0;
  }

  .online_el__inner {
    transition: none;
  }
  .online_cont.s--inactive .online_el__bg:before {
