/* START CSS VARIABLES */
:root {
    --body-background: #1f1f1f;
    --body-text: #ffffff;
    --container-background: #273349;
    --table-background: #1c223b;
    --table-background-hover: #151b31;
    --table-text: #e2e2e2;
    --pts-200: #d6a13f;
    --pts-300: #4480e7;
    --pts-600: #40c587;
    --delete-background: #242c4c;
    --delete-color: #909090;
    --delete-hover: #ff0000;
}
/* END CSS VARIABLES */


/* START CSS RESET + COMMON STYLES */
* {
    box-sizing: border-box;
}
body {
    -webkit-animation: fadeInPage ease 1.5s;
            animation: fadeInPage ease 1.5s; /* fade page in */
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1; /* fade page in */
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; /* fade page in */    
    justify-content: center;
    height: 100vh;
    background-color: #1f1f1f;
    background-color: var(--body-background);
    color: #ffffff;
    color: var(--body-text);
    /* font-family: 'Open Sans', sans-serif; */
    margin: 0;
}
h1 {
    text-align: center;
    margin: -2.5rem 0 0.625rem 0;
}
/* END CSS RESET + COMMON STYLES */


/* START OUTER CONTAINER STYLES */
.outer-container {
    background-color: #273349;
    background-color: var(--container-background);
    border-radius: 0.625rem;
    box-shadow: 0 5px 10px rgba(12,16,31,0.4);
    padding: 6.25rem;
    margin: auto;
    width: 90%;
    max-width: 62.5rem;
}

/* Start Table Styles */
table {
    color: #e2e2e2;
    color: var(--table-text);
    font-size: 0.875rem;
    padding: 0.625rem;
    width: 100%;
}
table th {
    font-size: 1rem;
}
table th, table td {
    padding: 0.9375rem;
    text-align: left;
}
table tbody tr {
    background-color: #1c223b;
    background-color: var(--table-background);
}
table tbody tr:hover {
    background-color: #151b31;
    background-color: var(--table-background-hover);
    box-shadow: 0 3px 5px rgba(0,0,0,0.2);
}
table tbody tr td:first-of-type {
    border-top-left-radius: 0.3125rem;
    border-bottom-left-radius: 0.3125rem;
}
table tbody tr td:last-of-type {
    border-top-right-radius: 0.3125rem;
    border-bottom-right-radius: 0.3125rem;
    text-align: center;
}
table tbody tr.pts-200 td:first-of-type {
    border-left: 0.3125rem solid #d6a13f;
    border-left: 0.3125rem solid var(--pts-200);
}
table tbody tr.pts-300 td:first-of-type {
    border-left: 0.3125rem solid #4480e7;
    border-left: 0.3125rem solid var(--pts-300);
}
table tbody tr.pts-600 td:first-of-type {
    border-left: 0.3125rem solid #40c587;
    border-left: 0.3125rem solid var(--pts-600);
}
table tbody tr td .fa-circle {
    transform: scale(0.7);
}
table tbody tr.pts-200 td .fa-circle {
    color: #d6a13f;
    color: var(--pts-200);
}
table tbody tr.pts-300 td .fa-circle {
    color: #4480e7;
    color: var(--pts-300);
}
table tbody tr.pts-600 td .fa-circle {
    color: #40c587;
    color: var(--pts-600);
}
.delete-btn {
    cursor: pointer;
    background-color: #242c4c;
    background-color: var(--delete-background);
    background-color: transparent;
    border: 0;
    border-radius: 0.125rem;
    color: #909090;
    color: var(--delete-color);
    font-size: 1.1rem;
    opacity: 0.2;
    padding: 0.3125rem 0.625rem;
}
.delete-btn:hover {
    color: #ff0000;
    color: var(--delete-hover);
}
.delete-btn:focus {
  outline: 0;
}
table tbody tr:hover .delete-btn {
    opacity: 1;
}
